<template>
    <div class="stok">
        <vx-card class="mb-base">
            <div class="md:flex">
                <div class="lg:w-1/6 mt-4 md:mt-0">
                    <v-select data-vv-validate-on="blur" :placeholder="'-- Pilih --'" :options="pilihanA" v-model="pilihanASelected" @input="updatePilihan"></v-select>
                </div>
                <div class="lg:w-1/3 mt-4 md:mt-0 md:ml-6">
                    <v-select data-vv-validate-on="blur" :placeholder="'-- Pilih --'" label="nama" :options="pilihanB" v-model="pilihanBSelected"  ></v-select>
                </div>
                <div class="lg:w-1/4 mt-4 md:mt-0 md:ml-6">
                    <datepicker data-vv-validate-on="blur" :placeholder="'-- Tanggal Awal --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="selectedStart" ></datepicker>
                </div>
                <div class="lg:w-1/4 mt-4 md:mt-0 md:ml-6">
                    <datepicker data-vv-validate-on="blur" :placeholder="'-- Tanggal Akhir --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="selectedEnd" ></datepicker>
                </div>
                <div class="w-10 mt-4 md:mt-0 md:ml-6">
                    <vs-button :disabled="!validateForm" radius color="primary" icon="search" v-on:click="loadDataServer"  style="display:inline; margin-right:30px;"></vs-button>
                </div>   
                <div class="mt-4 md:mt-0 md:ml-6">
                    <vs-button :disabled="!validateForm" class="py-2 px-3"  @click="tableToExcelStore('tableStore', 'Tracking Sales')" type="border" ><i class="feather icon-file-text"></i></vs-button>
                </div>   
            </div>
        </vx-card>
        <vx-card class="mt-5" v-show="card">
            <div class="table-wrapper">
                <table class="fixed-column" ref="myTable">
                    <thead>
                        <tr class="text-center">
                            <th class="kacab" v-for="(data,header) in headers.filter(item => item.label != 'Not' && item.label != 'Sales')" :key="header" :rowspan="data.rowspan" :colspan="data.colspan" :class="data.label" >{{ data.label }}</th>
                        </tr>
                        <tr class="text-center" >
                            <th v-for="(data,header) in headers.filter(item => item.label == 'Not' || item.label == 'Sales')" :key="header" :rowspan="data.rowspan" :colspan="data.colspan" style="z-index: 0;" >{{ data.label }}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr :ref="key === 0 ? 'intro' : null" v-for="(value,key) in table" :key="key">                            
                            <td v-for="(data,index) in value" :key="index" style="height:34px;" @click="loadGrafik(getItemValue(value))" v-html="data" ></td>
                        </tr>
                    </tbody>
                </table>
                <table hidden ref="tableStore">
                    <thead>
                        <tr class="text-center">
                            <th v-for="(data,header) in headers.filter(item => item.label != 'Not' && item.label != 'Sales')" :key="header" :rowspan="data.rowspan" :colspan="data.colspan" >{{ data.label }}</th>
                        </tr>
                        <tr class="text-center" >
                            <th v-for="(data,header) in headers.filter(item => item.label == 'Not' || item.label == 'Sales')" :key="header" :rowspan="data.rowspan" :colspan="data.colspan" >{{ data.label }}</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(value,key) in excel" :key="key" >
                            <td v-for="(data,index) in value" :key="index" style="height:34px;" v-html="data"></td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </vx-card>
        <vs-popup fullscreen title="Grafik Sales" :active.sync="popupActive">
            <apexchart height="400" ref="updateCharthourlySales" :options="chartOptionshourlySales" :series="seriesSales"></apexchart>
            <apexchart height="400" ref="updateCharthourlyNOT" :options="chartOptionshourlyNOT" :series="seriesNOT"></apexchart>

        </vs-popup>
    </div>
</template>

<script>
import moment from 'moment'
import Datepicker from 'vuejs-datepicker'
import VueApexCharts from 'vue-apexcharts'
import 'intro.js/introjs.css';
import introJs from 'intro.js';


export default {
    data() {
        return{
            uri :'data:application/vnd.ms-excel;base64,',
            template:'<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
            base64: function(s){ return window.btoa(unescape(encodeURIComponent(s))) },
            format: function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) },
            // Select Option 1
            pilihanA:['Region','Wilayah','Area','Cabang','Toko'],
            pilihanASelected:null,
            // Select Option 2
            pilihanB:[],
            pilihanBSelected:null,
            // Select 3
            selectedStart:null,
            // Select 4
            selectedEnd:null,
            // Selct Option 5
            selectedMonth:new Date(),
            // Excel
            excellStore: true,
            sort: {
                key: '',
                isAsc: false
            },
            headers: [],
            table: [],
            excel: [],
            chart: [],
            card: false,
            popupActive: false,
            chartOptionshourlySales: {
                chart: {
                    height: 350,
                    type: 'line',
                },
                stroke: {
                    curve: 'smooth',
                    width: [3]
                },
                fill: {
                    type:'solid',
                    opacity: [1],
                },
                colors: ['#008efb'],
                markers: {
                    size: 0
                },
                dataLabels: {
                    enabled: true,
                    formatter: function (val) {
                        return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    },
                    // offsetX: -10,  
                },
                yaxis: [
                    {
                        title: {
                            text: 'SALES',
                        },
                    },
                ],
                grid : {
                    padding: {
                        top: 0,
                        right: 30,
                        bottom: 0,
                        left: 30
                    }
                },
                tooltip: {
                    shared: true,
                    intersect: false,
                    y: {
                        formatter: function(value, { series, seriesIndex, dataPointIndex }) {
                            return series[seriesIndex][dataPointIndex].toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                        }
                    }
                },
            },
            chartOptionshourlyNOT: {
                chart: {
                    height: 350,
                    type: 'line',
                },
                stroke: {
                    curve: 'smooth',
                    width: [1, 3]
                },
                fill: {
                    type:'solid',
                    opacity: [1, 1],
                },
                colors: ['#008efb','#EA5455'],
                markers: {
                    size: 0
                },
                dataLabels: {
                    enabled: true,
                    enabledOnSeries: [1],
                    formatter: function (val) {
                    return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    },
                },
                yaxis: [
                    {
                    title: {
                        text: 'NOT',
                    },
                    },
                    {
                    opposite: true,
                    title: {
                        text: 'AVG',
                    },
                    },
                ],
                grid : {
                    padding: {
                    top: 0,
                    right: 30,
                    bottom: 0,
                    left: 30
                    }
                },
                tooltip: {
                    shared: true,
                    intersect: false,
                    y: {
                    formatter: function(value, { series, seriesIndex, dataPointIndex }) {
                        return series[seriesIndex][dataPointIndex].toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                    }
                    }
                },
            },
            seriesSales:[],
            seriesNOT:[],
        }
    },

    components:{
        Datepicker,
        apexchart: VueApexCharts,
        introJs,
    },

    computed: {
        validateForm() {
            return !this.errors.any() && this.pilihanBSelected != null && this.selectedStart != null && this.selectedEnd != null;
        },
    },

    methods:{
        tableToExcelStore(table, name){
            if (!table.nodeType) table = this.$refs.tableStore
            let ctx = {worksheet: name || 'Worksheet', table: table.innerHTML}
            let fileName = name + '_' + moment(this.selectedStart).format('DD-MM-YYYY')  + '_' + moment(this.selectedEnd).format('DD-MM-YYYY') ;
            let link = document.createElement('a');
            link.href = this.uri + this.base64(this.format(this.template, ctx));
            link.download = fileName;
            link.click();
        },

        updatePilihan(value) {  
            this.$vs.loading()
            this.$http.get('toko/'+ value)
            .then(response => {
                if(response.data.status == "OK"){
                    this.pilihanB = response.data.message
                }
                //else{
                //   this.$swal.fire(
                //     'Oopss !',
                //     response.data.message,
                //     'error'
                //   )
                // }
                })
            .catch(error => {error})
            setTimeout( ()=> {
                this.$vs.loading.close()
            }, 1000);
        },

        getItemValue(value) {
            return Object.values(value)[1];
        },

        loadDataServer() {
            this.$vs.loading()
            this.card = false;

            let postData = {
                kode : this.pilihanBSelected.kode,
                nama : this.pilihanBSelected.nama,
                tgl_awal:  btoa(this.selectedStart),
                tgl_akhir: btoa(this.selectedEnd),
            }

            this.$http.post('pos/dataSales2', postData)
            .then(response =>{
                if(response.data.status =='SUKSES'){
                    this.card = true;
                    this.headers = response.data.message.headers;
                    this.table = response.data.message.table;
                    this.excel = response.data.message.excel;
                    this.chart = response.data.message.chart;
                    
                    let guide = JSON.parse(localStorage.getItem('guide'));
                    if(guide == null || guide.sales != true) {
                    this.$nextTick(() => {
                        const tableElement = this.$refs.myTable;
                        const tbodyElement = tableElement.querySelector('tbody');
                        const trElements = tbodyElement.querySelectorAll('tr');
                        
                            if (trElements.length > 0) {
                                this.startIntro();
                            }
                        })
                    }

                }else{
                    this.$swal.fire(
                    'Gagal !',
                    response.data.message,
                    'error'
                    )
                }
            })
            .catch(error => {error})
            .finally(() =>{this.$vs.loading.close()})
        },

        loadGrafik(nama) {
            this.popupActive = true
            this.save_local();

            let filter_header = this.headers.filter(item => item.label != 'Not' && item.label != 'Sales');

            let labelArray = filter_header.map(obj => obj.label);
            labelArray.splice(0, 2);

            let filter = this.excel.filter(item => item[1] === nama);

            let myObject = filter.reduce((acc, obj) => {
                for (let key in obj) {
                    acc[key] = obj[key];
                }
                return acc;
            }, {});

            delete myObject['0'];
            delete myObject['1'];
            
            let filteredObject = {};

            for (let key in myObject) {
                if (key % 2 != 0) {
                    filteredObject[key] = myObject[key];
                }
            }

            let valueArray = Object.values(filteredObject);
           
            this.seriesSales = [
                {
                    "name": "SALES",
                    "type": "line",
                    "data": valueArray
                },
            ];

            this.$refs.updateCharthourlySales.updateSeries(this.seriesSales)
            this.$refs.updateCharthourlySales.updateOptions({
                xaxis:{
                    categories:  labelArray
                }
            })
            this.$refs.updateCharthourlySales.updateOptions({
                title:{
                    text: 'GERAI ' + nama
                }
            });

            //Grafik NOT
            
            let filter_chart = this.chart.filter(item => item[1] === nama);

            let myObjectnot = filter_chart.reduce((acc, obj) => {
                for (let key in obj) {
                    acc[key] = obj[key];
                }
                return acc;
            }, {});

            delete myObjectnot['0'];
            delete myObjectnot['1'];

            let filteredObjectNOT = {};
            let filteredObjectAVG = {};

            for (let key in myObjectnot) {
                if (key % 2 == 0) {
                    filteredObjectNOT[key] = myObjectnot[key];
                }
            }

            for (let key in myObjectnot) {
                if (key % 2 != 0) {
                    filteredObjectAVG[key] = myObjectnot[key];
                }
            }
            let valueArrayNOT = Object.values(filteredObjectNOT);
            let valueArrayAVG = Object.values(filteredObjectAVG);

            this.seriesNOT = [
                {
                    "name": "NOT",
                    "type": "column",
                    "data": valueArrayNOT
                },
                {
                    "name": "AVG",
                    "type": "line",
                    "data": valueArrayAVG
                },
            ];
            this.$refs.updateCharthourlyNOT.updateSeries(this.seriesNOT)
            this.$refs.updateCharthourlyNOT.updateOptions({
                xaxis:{
                    categories:  labelArray
                }
            })
            this.$refs.updateCharthourlyNOT.updateOptions({
                title:{
                    text: 'GERAI ' + nama
                }
            });
        },

        startIntro() {
            this.$nextTick(() => {
                const intro = introJs();
                intro.setOptions({
                    steps: this.table.map((value, key) => {
                        if (key === 0) {
                        const title = 'Grafik Sales';
                        const introContent = '<p>Silahkan klik nama gerai untuk melihat Grafik Sales</p><br/><img src="https://res.cloudinary.com/dph9nrhmr/image/upload/c_scale,w_268/v1686211876/chart_bo8rki.png" alt="Image Description">';
                        const introPosition = 'bottom-middle';

                        return {
                            element: this.$refs.intro[key],
                            intro: introContent,
                            title: title,
                            position: introPosition,
                        };
                        } else {
                        return null; // Hide Intro.js for other cases
                        }
                    }).filter(step => step !== null), // 
                });
                intro.start();
            });
            
        },

        save_local() {
            let guide = JSON.parse(localStorage.getItem('guide'));

            let data = ({"sales": true});
            let updateObj = { ...guide, ...data};
            localStorage.setItem('guide', JSON.stringify(updateObj));
        }
    }
}
</script>

<style scoped>

.table-wrapper{
  overflow-y: scroll;
  height: auto;
}

table {
    width: 100%
}

.fixed-column thead .Kacab {
    left: 0;
    z-index: 1;
    min-width: 198px;
}

.fixed-column thead .Gerai {
    left: 200px;
    z-index: 1;
    min-width: 198px;
}

.fixed-column thead th {
    position: -webkit-sticky; /* for Safari */
    position: sticky;
    top: 0;
    font-size: 12px;
    background: #7367f0;
    color: #fff;
    border: 2px solid #DDEFEF;
    padding: 10px 15px;
    text-align: center;
    white-space: nowrap;
    z-index: 0;
}

.fixed-column tbody td:nth-child(1) {
    left: 0;
    z-index: 1;
    text-align: left;
    word-wrap: break-word;
}
.fixed-column tbody td:nth-child(2) {
    left: 200px;
    z-index: 1;
    text-align: left;
    word-wrap: break-word;
}

.fixed-column tbody td {
    position: -webkit-sticky; /* for Safari */
    position: sticky;
    left: 0;
    padding: 10px 15px;
    white-space: nowrap;
    border: 2px solid #DDEFEF;
    background: #fff;
    color: #797777;
    text-align: center;
    width: auto;

}

.fixed-column tbody tr:hover td{
    cursor: pointer;
    background-color: #EBEAF7 ;
    color: #968DF3
} 
</style>